
.bills header {
  text-align: center;
  color: #fff;
  padding: 20px 0;
  margin: 0;
  border-bottom: 1px solid #ededed;
}
.bills header.positive {
  background: #1abc9c;
}
.bills header.negative {
  background: #e74c3c;
}
.header-title {
  margin: 0;
  outline: none;
  position: relative;
}
.header-title-inner {
  line-height: 20px;
  padding: 6px 0;
  position: relative;
}
.header-content {
  font-size: 25px;
  padding-top: 20px;
  position: relative;
}
.scroll-list {
  margin: 0;
  padding: 0;
}
.scroll-list-item {
  border-bottom: 1px solid #ededed;
  margin: 0;
  outline: none;
  position: relative;
}
.scroll-list-item-content {
  background: #fff;
  line-height: 20px;
  padding: 6px 0;
  position: relative;
}
.scroll-list-item-content-inner {
  margin: 6px 0;
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.scroll-list-item-content-inner .date {
  height: 28px;
  margin: -4px 0;
  position: relative;
  width: 98px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
}
.scroll-list-item-content-inner .title {
  display: inline-block;
  width: 294px;
}
.scroll-list-item-content-inner .value {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 0 24px 0 8px;
  min-width: 0;
  width: 0;
  word-wrap: break-word;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.bills footer {
  text-align: right;
}
.bills footer label {
  position: relative;
  display: inline-block;
}



